import request from '@/utils/request'
// 获取快递模版
export function getExpressCompanyinfoTemplate(id) {
  return request({
    url: `/order/business/expressCompany/getExpressCompanyinfoTemplate?shopId=${id}`,
    method: 'get',
  })
}
// 获取快递模版
export function findByConditionPage(data) {
  return request({
    url: `/order/business/expressCompany/findByConditionPage`,
    method: 'post',
    data: data
  })
}
// 删除物流公司
export function deleteExpressComPany(data) {
  return request({
    url: `/order/business/expressCompany/deleteExpressComPany`,
    method: 'post',
    data: data
  })
}
// 添加物流公司
export function insertExpressCompanyInfo(data) {
  return request({
    url: `/order/business/expressCompany/insertExpressCompanyInfo`,
    method: 'post',
    data: data
  })
}
// 添加物流公司
export function findExpressCompanys(id) {
  return request({
    url: `/order/business/expressCompany/findExpressCompanys?shopId=${id}`,
    method: 'get'
  })
}
