<template>
  <div class="pageContent">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small"> 重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="物流名称：">
            <el-input style="width: 203px" v-model="searchList.expressName" clearable placeholder="物流名称"></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container" style="margin-top:10px" shadow="never">
      <div class="flex-row-between" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
        </div>
        <div style="padding-right:20px;">
          <el-button type="text" icon="el-icon-circle-plus-outline" @click="showAdd">新增物流公司</el-button>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <div>
      <el-card style="margin-top:20px;" class="filter-container" shadow="never">
        <el-table v-loading="dataLoading" :data="list" style="width: 100%">
          <el-table-column
            type="index"
            width="50">
          </el-table-column>
          <el-table-column label="物流公司">
            <template slot-scope="scope">
              {{scope.row.expressName}}
            </template>
          </el-table-column>
          <el-table-column label="物流公司编码">
            <template slot-scope="scope">
              {{scope.row.expressCode}}
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button @click="deleteItem(scope.row)" type="text" size="small">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-card>
      <!-- 分页 -->
      <div class="pageDown">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="searchList.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="listTotal">
        </el-pagination>
      </div>
    </div>
    <!-- 新增物流 -->
    <el-dialog title="新增物流基本信息" :visible.sync="newExpShow" width="40%">
      <el-form v-loading="getSelectLoading" :model="newExpForm" ref="newExpForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="物流公司：">
          <el-select style="width:70%;" value-key="expressCode" v-model="newExpForm.list" multiple placeholder="请选择快递公司">
            <el-option v-for="(item,index) in postList" :key="index" :label="item.expressName" :value="item"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="newExpShow = false">取 消</el-button>
        <el-button :loading="addNewExpLoading" type="primary" @click="addNewExp">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { 
  getExpressCompanyinfoTemplate,
  findByConditionPage,
  deleteExpressComPany,
  insertExpressCompanyInfo
} from '@/api/logistics/expressTemplate'
export default {
  data(){
    return{
      postList:[],
      getSelectLoading:false,
      searchList:{
        pageNum:1,
        pageSize:5
      },              // 搜索栏数据
      list:[],                    // 列表数据
      listTotal:0,
      newExpShow:false,           // 新建物流公司弹出框
      addNewExpLoading:false,     // 新建物流公司确定loading
      newExpForm:{},              // 新建物流公司数据
      dataLoading:true
    }
  },
  created(){
    this.getData();
  },
  methods:{
    showAdd(){
      this.getSelect();
      this.newExpShow = true;
    },
    // 获取快递公司下拉框
    getSelect(){
      this.getSelectLoading = true;
      getExpressCompanyinfoTemplate(this.shop.shopId).then(res=>{
        console.log('获取快递公司下拉框',res)
        this.postList = res.data
        this.getSelectLoading = false;
      })
    },
    // 数据列表
    getData(){
      this.dataLoading = true;
      console.log('请求列表的数据',this.searchList)
      this.searchList.shopId = this.shop.shopId
      findByConditionPage(this.searchList).then(res=>{
        console.log('获取数据列表',res)
        this.list = res.data.list;
        this.listTotal = res.data.total
        this.dataLoading = false;
      })
    },
    // 搜索栏重置
    reset(){
      this.searchList.expressName = '';
      this.getData()
    },
    // 删除
    deleteItem(e){
      console.log(e)
      this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        deleteExpressComPany({id:e.id}).then(res=>{
          console.log('删除',res)
          if(res.code == 200){
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
            this.getData()
            this.getSelect()
          }else{
            this.$message.error(res.message);
          }
        })
        
      }).catch(() => {});
    },
    // 新增物流 - 点击确定
    addNewExp(){
      console.log(this.newExpForm)
      if(this.newExpForm.list.length == 0){
        this.$message.error('请至少选择一家快递公司');
        return;
      }
      this.addNewExpLoading = true;
      this.newExpForm.shopId = this.shop.shopId
      insertExpressCompanyInfo(this.newExpForm).then(res=>{
        console.log('添加',res)
        if(res.code == 200){
          this.$message({
            type: 'success',
            message: '新增成功!'
          });
          this.getData()
          this.getSelect()
          this.newExpShow = false;
          this.newExpForm.list = []
          setTimeout(()=>{this.addNewExpLoading = false},1000)
        }else{
          this.addNewExpLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    handleSizeChange(val) {
      this.searchList.pageSize = val;
      this.getData()
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.searchList.pageNum = val;
      this.getData()
      console.log(`当前页: ${val}`);
    }
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .pageDown{
    padding: 20px 0;
  }
</style>